import React, { Component } from "react";
import "./AllSchedule.scss";
import TableComponent from "../../components/Controls/Table/sortableTable";
import CalenderCard from "../../components/Controls/calenderCard/calenderCard";
import moment from "moment";
import * as JDActions from "../../store/JobDescription/JobDescriptionAction";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import CustomSelect from "../../components/Atoms/customSelect/customSelect";
import * as ScheduleActions from "../../store/Schedule/scheduleAction";
import ResponseIcon from "../../assets/images/invite.svg";
import InviteIcon from "../../assets/images/inviteIcon.svg";
import ArrowRightIcon from "@material-ui/icons/ArrowRight";
import Modal from "@material-ui/core/Modal";
import ReactGA from "react-ga";

let queryParams = new URLSearchParams(window.location.search);

class AllSchedule extends Component {
  state = {
    page: 1,
    limit: 10,
    startDate: moment().subtract(7, "d"),
    endDate: moment().add(1, "d"),
    totalPage: 0,
    jdOptions: [],
    jdSeleted: [],
    tableData: [],
    viewMessageModal: false,
    message: "",
  };

  componentDidMount() {
    ReactGA.pageview(window.location.pathname);
    this.props.ScheduleAction.getSchedule({
      page: this.state.page,
      limit: this.state.limit,
      from: new Date(this.state.startDate).toISOString(),
      to: new Date(this.state.endDate).toISOString(),
    });
    this.props.JDAction.getJobDescriptionData();
  }

  async componentDidUpdate(prevProps) {
    const cur = this.props && this.props.scheduleReducer;
    const prev = prevProps && prevProps.scheduleReducer;
    const curJd = this.props && this.props.JDReducer;
    const prevJd = prevProps && prevProps.JDReducer;
    if (
      prevJd.getJobDescriptionData !== curJd.getJobDescriptionData &&
      curJd.getJobDescriptionData &&
      curJd.getJobDescriptionData.length > 0
    ) {
      const options = curJd.getJobDescriptionData.map((el) => ({
        label: el.title + " " + el.minExp + "-" + el.maxExp,
        value: el.id,
      }));
      this.setState({ jdOptions: options });

      if (queryParams.get("JD")) {
        let JD = queryParams.get("JD");
        let JDArray = JD.split(",");
        let newJD = [];
        options.map((item) => {
          if (JDArray.includes(item.value)) {
            newJD.push(item);
          }
        });
        await this.setState({
          jdSeleted: newJD,
        });
      }
      if (queryParams.get("page")) {
        let page = queryParams.get("page");
        if (page) {
          await this.setState({
            page: parseInt(page),
          });
        }
      }
      if (queryParams.get("limit")) {
        let limit = queryParams.get("limit");
        if (limit) {
          await this.setState({
            limit: parseInt(limit),
          });
        }
      }
      if (queryParams.get("startDate") && queryParams.get("endDate")) {
        await this.setState({
          startDate: queryParams.get("startDate"),
          endDate: queryParams.get("endDate"),
        });
      }

      this.reuseApiCall();
    }
    if (
      prev.scheduleData !== cur.scheduleData &&
      cur.scheduleData &&
      cur.scheduleData.data
    ) {
      const { results, totalPages } = cur.scheduleData.data;
      const data =
        results &&
        results.length > 0 &&
        results.map((el, index) => {
          const date = el && el.createdAt && new Date(el.createdAt);
          return {
            date: `${
              index + (this.state.page - 1) * this.state.limit + 1
            }. ${date.getDate()}-${date.toLocaleString("default", {
              month: "short",
            })}-${date.getFullYear()}`,
            jobTitle: el && el.jdId && el.jdId.title,
            assessment: el && el.assessmentId && el.assessmentId.name,
            startDate:
              el &&
              el.fromTimestamp &&
              new Date(el.fromTimestamp).toLocaleDateString(),
            endDate:
              el &&
              el.toTimestamp &&
              new Date(el.toTimestamp).toLocaleDateString(),
            device:
              el && el.modeOfTest === "LAPTOP"
                ? "PC"
                : el && el.modeOfTest === "MOBILE"
                ? "Mobile"
                : el && el.modeOfTest === "ALL_DEVICE"
                ? "Both"
                : "",
            invites: el && el.candidates && el.candidates.length,
            response: el && el.responseCount,
            message: el && el.scheduleMessage,
            data: el,
            id: el._id,
          };
        });
      this.setState({ tableData: data, totalPage: totalPages });
    }
  }
  reuseApiCall = () => {
    const { page, limit, jdSeleted, startDate, endDate } = this.state;
    let jdIds = "";

    jdSeleted &&
      jdSeleted.length > 0 &&
      jdSeleted.map((el, index) =>
        index === 0 ? (jdIds = el.value) : (jdIds = jdIds + "," + el.value)
      );
    const payload = {
      page: page,
      limit: limit,
      from: new Date(startDate).toISOString(),
      to: new Date(endDate).toISOString(),
      jdId: jdIds,
    };
    this.props.ScheduleAction.getSchedule(payload);
  };
  setStartDate = (date) => {
    queryParams.set(
      "startDate",
      new Date(date.selection.startDate).toISOString()
    );
    queryParams.set("endDate", new Date(date.selection.endDate).toISOString());
    window.history.replaceState(null, null, "?" + queryParams.toString());
    this.setState(
      {
        startDate: new Date(date.selection.startDate).toISOString(),
        endDate: new Date(date.selection.endDate).toISOString(),
        page: 1,
      },
      this.reuseApiCall
    );
  };
  handleBackPagination = () => {
    if (this.state.page !== 1) {
      queryParams.set("page", this.state.page - 1);
      window.history.replaceState(null, null, "?" + queryParams.toString());
      this.setState({ page: this.state.page - 1 }, this.reuseApiCall);
    }
  };
  handleNextPagination = () => {
    if (this.state.page !== this.state.totalPage) {
      queryParams.set("page", this.state.page + 1);
      window.history.replaceState(null, null, "?" + queryParams.toString());
      this.setState({ page: this.state.page + 1 }, this.reuseApiCall);
    }
  };
  handlePagination = (e) => {
    queryParams.set(
      "page",
      e > 0 && e <= this.state.totalPage ? e : this.state.page
    );
    window.history.replaceState(null, null, "?" + queryParams.toString());
    this.setState(
      {
        page: e > 0 && e <= this.state.totalPage ? e : this.state.page,
      },
      this.reuseApiCall
    );
  };
  selectJds = (e) => {
    let JD = [];
    e.map((item) => {
      JD.push(item.value);
    });
    queryParams.set("JD", JD);
    window.history.replaceState(null, null, "?" + queryParams.toString());
    this.setState({ jdSeleted: e, page: 1 }, this.reuseApiCall);
  };
  invitePage = (data) => {
    this.props.history.push({
      pathname: `/invites/${data && data.id}`,
    });
  };
  responsePage = (data) => {
    this.props.history.push({
      pathname: `/response`,
      search: `?JDs=${data.data.jdId._id}&schedule=${data.id}`,
    });
  };
  viewMessageHandler = (data) => {
    this.setState({ viewMessageModal: true, message: data && data.message });
  };

  setLimit = async (e) => {
    queryParams.set("limit", e);
    window.history.replaceState(null, null, "?" + queryParams.toString());
    this.setState(
      {
        limit: e,
        page: 1,
      },
      () => {
        this.reuseApiCall();
      }
    );
  };
  watiHandler = (data) => {
    window.open(
      `https://live-8103.wati.io/v2/teamInbox/62285384d146cb2823aaac56?filter=%7B%22filterAttribute%22:[%7B%22filterType%22:0,%22name%22:%22scheduleid%22,%22operator%22:%22contain%22,%22value%22:%22${
        data && data.id
      }%22%7D],%22filterType%22:5%7D`
    );
  };
  render() {
    const {
      startDate,
      endDate,
      jdOptions,
      jdSeleted,
      tableData,
      viewMessageModal,
      message,
    } = this.state;

    let column = [
      {
        Header: () => <div style={{ textAlign: "center" }}>Date</div>,
        minWidth: 100,
        accessor: "date",
        style: { whiteSpace: "initial" },
      },
      {
        Header: () => <div style={{ textAlign: "center" }}>Job Title</div>,
        minWidth: 180,
        accessor: "jobTitle",
        style: { whiteSpace: "initial" },
        Cell: (val) => (
          <div
            className='tableJT'
            onClick={() => this.invitePage(val && val.original)}
            style={{ color: "#2469CF", cursor: "pointer" }}
          >
            {val.value}
          </div>
        ),
      },
      {
        Header: () => (
          <div style={{ textAlign: "center", overflow: "auto" }}>
            Assessment
          </div>
        ),
        minWidth: 150,
        accessor: "assessment",
        style: { whiteSpace: "initial" },
      },
      {
        Header: () => <div style={{ textAlign: "center" }}>Startdate</div>,
        minWidth: 100,
        accessor: "startDate",
        style: { whiteSpace: "initial" },
      },
      {
        Header: () => <div style={{ textAlign: "center" }}>Enddate</div>,
        minWidth: 100,
        accessor: "endDate",
        style: { whiteSpace: "initial" },
      },
      {
        Header: () => <div style={{ textAlign: "center" }}>Device</div>,
        minWidth: 60,
        accessor: "device",
        style: { whiteSpace: "initial" },
      },
      {
        Header: () => <div style={{ textAlign: "center" }}>Invites</div>,
        minWidth: 80,
        accessor: "invites",
        sortable: false,
        Cell: (val) => (
          <div
            className='tableInvite'
            onClick={() => this.invitePage(val && val.original)}
          >
            <img src={InviteIcon} alt='' />{" "}
            <div className='value'>{val.value}</div>
            <ArrowRightIcon className='rightIcon' />
          </div>
        ),
      },
      {
        Header: () => <div style={{ textAlign: "center" }}>Response</div>,
        minWidth: 80,
        accessor: "response",
        sortable: false,
        Cell: (val) => (
          <div
            className='tableInvite'
            onClick={() => this.responsePage(val && val.original)}
          >
            <img src={ResponseIcon} alt='' />{" "}
            <div className='value'>{val.value}</div>
            <ArrowRightIcon className='rightIcon' />
          </div>
        ),
      },
      {
        Header: () => <div style={{ textAlign: "center" }}>Message</div>,
        minWidth: 100,
        accessor: "message",
        sortable: false,
        Cell: (val) => (
          <div
            className='ViewMessage'
            onClick={() => this.viewMessageHandler(val && val.original)}
          >
            View Message <ArrowRightIcon className='rightIcon' />
          </div>
        ),
      },
      {
        Header: () => <div style={{ textAlign: "center" }}>WATI</div>,
        minWidth: 80,
        accessor: "wati",
        sortable: false,
        Cell: (val) => (
          <div
            className='ViewMessage'
            onClick={() => this.watiHandler(val && val.original)}
          >
            Click <ArrowRightIcon className='rightIcon' />
          </div>
        ),
      },
    ];

    return (
      <div className='AllScheduleMain'>
        <div className='FilterArea'>
          <div className='JobDropDown'>
            <CustomSelect
              options={jdOptions}
              selected={jdSeleted || []}
              placeholder='Select JD'
              handleChange={this.selectJds}
              clearAllSelected={() =>
                this.setState(
                  { jdSeleted: [] }
                  //this.reuseApiCall
                )
              }
            />
          </div>
          <div className='date-card' style={{ marginTop: "-8px" }}>
            <CalenderCard
              legend={"Invite Date"}
              startDate={startDate}
              endDate={endDate}
              handleSelectDate={(date) => this.setStartDate(date)}
            />
          </div>
        </div>
        <div>
          <TableComponent
            className={"jd-table"}
            data={tableData}
            columns={column}
            sortable={true}
            showBottomPagination={true}
            pageSize={this.state.limit}
            setPageSize={(e) => this.setLimit(e)}
            gotoPage={(e) => this.handlePagination(e)}
            totalPage={this.state.totalPage}
            pageNumber={this.state.page ? this.state.page : 1}
            handlePrev={this.handleBackPagination}
            handleNext={this.handleNextPagination}
          />
        </div>
        <Modal
          className='messageModalSchedule'
          open={viewMessageModal}
          onClose={() => this.setState({ viewMessageModal: false })}
        >
          <div
            dangerouslySetInnerHTML={{ __html: message }}
            className='messageDiv'
          ></div>
        </Modal>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    JDReducer: state.JobDescriptionReducer,
    scheduleReducer: state.ScheduleReducer,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    JDAction: bindActionCreators(JDActions, dispatch),
    ScheduleAction: bindActionCreators(ScheduleActions, dispatch),
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(AllSchedule));
